                                <li><a href="#" onClick={(e) => {e.preventDefault(); setTestiMonialPTI(false); setTestiMonialGrow(true); setTestiMonialTriangle(false)}}><StaticImage src='../images/Grow_Marketing_NetMinded.png' /></a></li>
